.project-page {
  margin: 0 7px;
  background: linear-gradient(to right, #355c7d, #6c5b7b, #6c5b7b, #355c7d);
  height: 76vh;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  color: #fff;
  font-weight: 700;

  .image {
    width: 12rem;
    height: 22vh;
    background-size: cover;
    background-position: center;
    margin-bottom: 5px;
  }
}
